body{
    background-color: #0f0f0f;
}

.loading {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    color: rgb(247, 41, 41);
    font-size: 45px;
    letter-spacing: -0.8px;
    text-shadow: 6px 6px 10px rgba(56, 56, 56, 0.665);
}

